<template>
  <form class="form-wrapper">
    <div class="info">请输入以下信息</div>
    <div class="form-input">
      <label>登录名:</label>
      <input type="text" v-model="name">
    </div>
    <div class="form-input">
      <label>密码:</label>
      <input type="password" v-model="password">
    </div>
    <div class="form-input">
      <label>再次输入密码:</label>
      <input type="password" v-model="passwordRepeart">
    </div>
    <div class="btns">
      <button type="button" @click="reg()" class="reg">确认注册</button>
      <button type="button" @click="()=>{this.$router.back()}" class="cancel">取消</button>
    </div>
    <div>
    </div>
  </form>
</template>

<script>
export default {
  name: "RegForm.vue",
  data () {
    return {
      name: '',
      password: '',
      passwordRepeart: ''
    }
  },
  methods: {
    reg () {
      if (this.password === this.passwordRepeart && this.name != ''){
        localStorage.setItem("name", this.name)
        localStorage.setItem("password", this.password)
        alert('成功注册')
        this.$router.push({name:'login'})
      }else{
        alert('请检查密码是否输入一致')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.info{
  padding: 30px;
  font-size: 18px;
  font-weight: bold;
}
label{
  display:inline-block;
  width:100px;
}
.form-wrapper{
  display:flex;
  justify-content:center;
  align-items:center;
  flex-direction:column;
  .btns{
    margin-top:20px;
    .reg{
      margin-right:20px;
    }
  }
}
</style>
